import React, { ReactNode, HTMLAttributes } from 'react';
import { createComponent } from '../lib';
import type { JSX } from '@vime/core/dist/types';

import { 
  VmEndTime,
  VmTime 
} from '@vime/core';

import { define } from '../lib';
  

define('vm-end-time', VmEndTime);
define('vm-time', VmTime);
  

export interface EndTimeProps extends JSX.VmEndTime, HTMLAttributes<HTMLVmEndTimeElement> {
  children?: ReactNode | ReactNode[]
};

export default createComponent<HTMLVmEndTimeElement, EndTimeProps>(
  'vm-end-time',
  new Set(['duration','i18n','alwaysShowHours']),
);
  